.logo {
  display: block !important;
  margin-left: auto;
  margin-right: auto;
}
.login-wrapper {
  padding-top: 5rem !important;
}


.page-wrapper {
  background: url(./assets/bg-mob.png) center no-repeat !important;
  background-size: cover;
  width: 100vw;
  min-height: 100vh;
  background-size: cover !important;
}
.smiley-wrapper{
  display: flex;
  justify-content: center;
  align-items: center
}
.smiley {
  width: 7rem;
}
.qst-wrapper{
  width: 100vw !important;
  height: 35px;
  border-radius: 5px;
}
.qst-list {
  width: 100vw !important;
  word-wrap: break-word;
}

.submit {
  background: #9b25cd !important;
  background-position: center !important;
  transition: background 1s !important;
}
.submit:hover {
  background: #47a7f5 radial-gradient(circle, transparent 1%, #47a7f5 1%) center/15000% !important;
}
.submit:active {
  background-color: #6eb9f7 !important;
  background-size: 100% !important;
  transition: background 0s !important;
}
.social-icon {
  width: 2rem;
  padding-right: 1rem;
}
.drp-content {
  margin-right: 7rem !important;
}
.text-box {
  padding: 16px;
  position: fixed;
  bottom: 0;
  width: 100%;
  background-image: url(./assets/bg-mob.png);
}
.admin-alerts {
  border: solid 2px white !important;
}

.myMessages {
  text-align: right;
}

.myMessages, .theirMessages {
  min-width: 2rem;
  max-width: 100vw;
  overflow-x: hidden;
  padding: 1rem;
}
.sender, .reciever {
  font-size: 12px;
  color: whitesmoke;
}
.name-wrapper, .text-wrapper {
  display: inline;
  max-width: 80vw;
}
.sentMessage, .recievedMessage {
  border-radius: 10px;
  padding: 12px;
  display: inline-block !important;
  max-width: 100%;
  word-wrap: break-word;
}
.message-container {
  max-height: 65vh !important;
  max-width: 100vw !important;
  overflow-x: hidden;
  overflow-y: scroll;
  
}
.footer {
  position: relative; 
  top: 10px;
  text-align: center;  
  color: white; 
  font-size: 12px; 
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; 
  font-weight: bolder;
  color: #333333;
}